import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import jwtDecode from "jwt-decode";
export default {
  setup() {
    const show = ref(false);
    const show_1 = ref(false);
    const show_2 = ref(false);
    const show_3 = ref(false);
    const show_4 = ref(false);
    const message = ref("");
    const checked = ref();
    const router = useRouter();
    const currentTime = ref(["12", "00"]);
    const time = ref("");
    const text = ref("");
    const userId = ref("");
    const remindSwitch = ref("");
    const reValue = ref("");
    const userValue = ref("");
    const token = localStorage.getItem("jwtToken"); // 从localStorage获取JWT令牌

    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    onMounted(() => {
      show_1.value = true;
      fetchLoginToken();
    });
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        const decodedToken = jwtDecode(token);
        // 从解码后的令牌中获取特定的数据
        userId.value = decodedToken.id; // 从令牌中获取用户ID
        fetchDataAndFillForm();
        // 在这里处理登录令牌接口的响应
        // 如果需要执行一些特定的操作，可以在这里添加代码
      }).catch(error => {
        console.error("请求loginToken接口失败", error);
        router.replace("/login");
      });
    };
    const userLogin = ref("");
    const fetchDataAndFillForm = () => {
      try {
        console.log(userId);
        axios.get(`/api/selectUser?id=${userId.value}`, {
          headers
        }).then(response => {
          const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
          console.log(data);
          remindSwitch.value = data.remindSwitch;
          const parts = data.remindTime.split(" ");
          const minutes = parts[1];
          const hours = parts[2];
          checked.value = remindSwitch.value;
          // 格式化为 "hh:mm" 格式
          time.value = `${hours}:${minutes}`;
          text.value = data.remindText;
          currentTime.value = [hours, minutes];
        });
        axios.get("/api/signPassword", {
          headers
        }).then(response => {
          const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
          console.log(data);
          userLogin.value = data;
        });
      } catch (error) {
        console.error("获取数据失败", error);
      }
      show_1.value = false;
    };
    const change = () => {
      if (checked.value == true) {
        axios.get("/api/openRemind", {
          headers
        }).then(response => {
          const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
          console.log(data);
          checked.value = true;
          remindSwitch.value = true;
        });
      } else {
        axios.get("/api/closeRemind", {
          headers
        }).then(response => {
          const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
          console.log(data);
          checked.value = false;
          remindSwitch.value = false;
        });
      }
    };
    const confirm = currentTime => {
      console.log(currentTime);
      const minutes = currentTime[1];
      const hours = currentTime[0];
      const cronExpression = `0 ${minutes} ${hours} * * ?`;
      axios.get(`/api/changeRemind?time=${cronExpression}`, {
        headers
      }).then(response => {
        const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
        console.log(data);
        time.value = `${hours}:${minutes}`;
        message.value = data;
        show.value = false;
      });
      show_2.value = true; // 显示
      // 等待两秒后执行关闭操作
      setTimeout(() => {
        show_2.value = false; // 关闭
      }, 1000); // 2000毫秒等于2秒
    };

    const changeTime = () => {
      show.value = true;
    };
    const cancel = () => {
      show.value = false;
    };
    const requireItem = () => {
      show_3.value = true;
      return;
    };
    const reCheckConfirm = () => {
      const user = {
        nameId: userId.value,
        remindText: reValue.value
      };
      fetch("/api/requireUser", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify(user)
      }).then(response => response.json()) // 将 .json() 放在正确的位置
      .then(data => {
        if (data.code == 1) {
          console.log(data.data);
          try {
            message.value = data.data;
            text.value = reValue.value;
          } catch (error) {
            console.error("请求requireUser接口失败", error);
          }
        }
      }).catch(error => {
        console.error("请求requireUser接口失败", error);
        // 处理请求失败的情况
      });

      show_2.value = true; // 显示
      // 等待两秒后执行关闭操作
      setTimeout(() => {
        show_2.value = false; // 关闭
      }, 1000); // 2000毫秒等于2秒
    };

    const reCheckCancel = () => {
      return;
    };
    const requireUser = () => {
      show_4.value = true;
    };
    const userCheckConfirm = () => {
      console.log(userValue.value);
      axios.get(`/api/RequireSignPassword?password=${userValue.value}`, {
        headers
      }).then(response => {
        const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
        message.value = data;
        userLogin.value = userValue.value;
      });
      show_2.value = true; // 显示
      // 等待两秒后执行关闭操作
      setTimeout(() => {
        show_2.value = false; // 关闭
      }, 1000); // 2000毫秒等于2秒
    };

    const userCheckCancel = () => {
      return;
    };
    const onClickLeft = () => router.replace("/User");
    return {
      reValue,
      changeTime,
      show,
      show_1,
      show_2,
      show_3,
      show_4,
      confirm,
      cancel,
      userId,
      text,
      time,
      change,
      checked,
      currentTime,
      remindSwitch,
      onClickLeft,
      message,
      requireItem,
      reCheckConfirm,
      reCheckCancel,
      userLogin,
      userValue,
      requireUser,
      userCheckConfirm,
      userCheckCancel
    };
  }
};